*, *::before, *::after { box-sizing: border-box; }
html, body {
  min-height: 100%;
  margin: 0;
  padding: 0;
}
h1, h2, h3 {
  margin: 0;
}

body {
  font-family: 'Hammersmith One', sans-serif;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 80px;
}

button {
  border: 0;
  padding: 1rem;
  background-color: rgba(15,65,266, .65);
  cursor: pointer;
  border-radius: 4px;
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 300ms;

  &:hover {
    background-color:  rgba(15,65,266, 1);
  }
}

